html {
  font-size: 10px;
}

body {
  margin: 0;
  background-color: #000;
  color: #fff;
  font-size: 2rem;
}

button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  font-size: 24px;
}

.hidden {
  opacity: 0;
}

.application {
  width: calc(100vw - 35px);
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 20px;
}

.container {
  position: relative;
  left: 0;
  transition: left 2s ease;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
  margin-bottom: 10px;
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-height: 550px;
  margin-bottom: 10px;
}

.content div {
  width: 100%;
}
.content div.category-image {
  width: 195px;
}
.content div.artists-picture {
  width: 480px;
}
.content div.artists-answers {
  width: 80%;
}
.content div.pictures-wrapper {
  width: 480px;
}

.hide {
  left: -2000px;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - 50px);
  max-width: 1440px;
}

.footer-logo {
  width: 85px;
  height: 31px;
  background-image: url("../img/svg/rs_school.svg");
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 170px;
}
.info a {
  text-decoration: none;
  color: #fff;
}

.button {
  display: block;
  width: 287px;
  height: 62px;
  background-color: transparent;
  text-align: center;
  color: #fff;
  border: 1.5px solid white;
  border-radius: 60px;
  transition: all 0.5s ease;
  cursor: pointer;
}
.button:hover {
  transform: scale(1.02);
  box-shadow: 5px 5px 15px rgba(255, 255, 255, 0.7);
  background-color: #ffbca2;
  color: #000;
}

.button_colored {
  border: none;
  color: #000;
  background-color: #ffbca2;
}

.button_element {
  margin-right: 20px;
}

.home-route,
.categories-route,
.settings-route {
  cursor: pointer;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(1, 1, 1, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal {
  position: relative;
  border-radius: 20px;
  background-color: #ffffff;
  width: 450px;
  min-height: 500px;
  max-height: 90%;
}
.modal-picture {
  display: block;
  position: relative;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 20px;
}
.modal-result {
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 255px;
  left: 90px;
  background-repeat: no-repeat;
  background-size: contain;
}

.correct {
  background-image: url("../img/svg/correct.svg");
}

.wrong {
  background-image: url("../img/svg/wrong.svg");
}

.modal-text {
  color: #000;
  text-align: center;
}

.modal-buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.button-modal {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
}

.container-main {
  background-image: url("../img/bg-main.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.settings {
  width: 36px;
  height: 36px;
  margin-left: auto;
  background-image: url("../img/svg/settings.svg");
  transition: transform 0.8s ease-in-out;
}
.settings:hover {
  transform: rotate(180deg);
}

.logo {
  width: 100%;
  height: 165px;
  background-image: url("../img/logo-main.png");
  background-repeat: no-repeat;
  background-position: center;
}

.content .button-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 600px;
}

@media (max-width: 650px) {
  .content .button-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .button-category {
    margin-bottom: 10px;
  }

  .logo {
    background-size: 280px;
  }
}
.setting-title {
  margin: 0;
  margin-left: 10px;
  position: relative;
  font-size: 2rem;
  line-height: 2.8rem;
}
.setting-title::before {
  content: "";
  background-image: url("../img/svg/left_arrow.svg");
  width: 10px;
  height: 20px;
  position: absolute;
  top: 4px;
  left: -15px;
}

.exit {
  width: 36px;
  height: 36px;
  background-image: url("../img/svg/exit.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.content-settings {
  margin-top: 3%;
}
.content-settings > div {
  margin-bottom: 2%;
}
.content-settings .setting-time {
  display: flex;
  align-items: center;
  margin-bottom: 0;
}
.content-settings .setting-time button {
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 100%;
  background-color: #fff;
  font-size: 20px;
}
.content-settings .setting-time button span {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.content-settings .setting-volume {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
  width: 225px;
}
.content-settings .setting-volume button {
  width: 36px;
  height: 36px;
  background-repeat: no-repeat;
  background-position: center;
}
.content-settings .setting-timer-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 8%;
}
.content-settings .setting-timer-wrapper .setting-timer {
  width: 50px;
  height: 24px;
  border-radius: 34px;
  background-color: #ffbca2;
  position: relative;
}
.content-settings .setting-timer-wrapper .setting-timer .slider {
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 100%;
  position: absolute;
}

.subsetting-title {
  margin: 0;
  margin-bottom: 1%;
  font-size: 2.5rem;
}

.mute {
  background-image: url("../img/svg/mute.svg");
}

.sound {
  background-image: url("../img/svg/volume.svg");
}

#volume-bar {
  appearance: none;
  height: 3px;
  width: 220px;
  cursor: pointer;
  border-radius: 4.5px;
  background: linear-gradient(to right, #ffbca2 0%, #ffbca2 30%, #a4a4a4 30%, #a4a4a4 100%);
}
#volume-bar::-webkit-slider-thumb {
  appearance: none;
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 100%;
}

.on {
  left: 0;
  right: auto;
}

.off {
  left: auto;
  right: 0;
}

#time-input {
  width: 50px;
  padding: 0;
  border: none;
  background-color: transparent;
  position: relative;
}

.time-input-value {
  position: absolute;
  left: 36px;
}

.setting-buttons {
  display: flex;
  justify-content: flex-end;
}

.navigation {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0;
  list-style-type: none;
}
.navigation-links {
  width: 50px;
}
.navigation-links li:hover {
  color: #ffbca2;
  cursor: pointer;
}

.content_categories {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  column-gap: 15px;
  row-gap: 5px;
  justify-items: center;
  max-width: 1440px;
}

.category {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 195px;
  max-height: 239px;
}
.category-statistics {
  flex-basis: 65%;
  padding-right: 5px;
  text-align: right;
}
.category-name {
  margin: 0;
  flex-basis: 30%;
  text-align: center;
  font-size: 4rem;
  color: #ffbca2;
}
.category-image {
  height: 206px;
  border-radius: 20px;
}

.logo-small {
  width: 153px;
  height: 60px;
  margin-right: 30px;
  background-image: url("../img/logo-small.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

@media (max-width: 1260px) {
  .content_categories {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 260px);
  }
}
@media (max-width: 850px) {
  .content_categories {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 260px);
  }
}
@media (max-width: 630px) {
  .content_categories {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(6, 260px);
  }
}
@media (max-width: 420px) {
  .content_categories {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(12, 260px);
  }
}
.container-question {
  max-width: 826px;
  margin: 0 auto;
}

.timer-input {
  width: 80%;
  appearance: none;
  height: 3px;
  cursor: pointer;
  border-radius: 4.5px;
  background: linear-gradient(to right, #ffbca2 0%, #ffbca2 30%, #a4a4a4 30%, #a4a4a4 100%);
}
.timer-input::-webkit-slider-thumb {
  appearance: none;
  width: 12px;
  height: 12px;
  background-color: #fff;
  border-radius: 100%;
}

.question-text {
  margin: 0;
  margin-bottom: 5px;
  text-align: center;
  width: 100%;
}

.artists-picture {
  width: 350px;
  height: 350px;
  margin: 0 auto;
  margin-bottom: 15px;
  background-image: url("https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  border-radius: 20px;
  position: relative;
}

.artists-answers {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 15px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 50px);
  gap: 10px;
}

.artists-answer {
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #fff;
  border-radius: 10px;
}
.artists-answer:hover {
  cursor: pointer;
  box-shadow: 5px 5px 15px rgba(255, 255, 255, 0.7);
  background-color: #ffbca2;
  color: #000;
}

.pagination {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 50%;
  height: 20px;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
}
.pagination li {
  width: 10px;
  height: 10px;
  background-color: #a4a4a4;
  border-radius: 100%;
}
.pagination .wrong {
  background-color: #8B0000;
}
.pagination .right {
  background-color: #006400;
}

.pictures-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
  height: 372px;
}

.answer {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 10px;
}

#variant1 {
  background-image: url("https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg");
}

#variant2 {
  background-image: url("https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg");
}

#variant3 {
  background-image: url("https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg");
}

#variant4 {
  background-image: url("https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg");
}

@media (max-width: 580px) {
  .artists-answers {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(4, 50px);
  }
}
@media (max-width: 460px) {
  .timer-input {
    width: 190px;
  }

  .artists-picture {
    width: 280px;
    height: 280px;
  }
}