.container-question {
    max-width: 826px;
    margin: 0 auto;
}

.timer-input {
    width: 80%;
    appearance: none;
    height: 3px;
    cursor: pointer;
    border-radius: 4.5px;
    background: linear-gradient(
        to right,
        $color-active 0%,
        $color-active 30%,
        $color-grey 30%,
        $color-grey 100%
      );
    &::-webkit-slider-thumb {
      appearance: none;
      width: 12px;
      height: 12px;
      background-color: $color-text-on-dark;
      border-radius: 100%;
    }  
}

.question-text {
    margin: 0;
    margin-bottom: 5px;
    text-align: center;
    width: 100%;
}

.artists-picture {
    width: 350px;
    height: 350px;
    margin: 0 auto;
    margin-bottom: 15px;
    background-image: url('https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    border-radius: 20px;
    position: relative;
}

.artists-answers {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 15px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 50px);
    gap: 10px;
}

.artists-answer {
    padding: 10px;
    box-sizing: border-box;
    text-align: center;
    border: 1px solid $color-text-on-dark;
    border-radius: 10px;
    &:hover {
        cursor: pointer;
        box-shadow: 5px 5px 15px rgba(255, 255, 255, 0.7);
        background-color: $color-active;
        color: $color-background;
    }
}

.pagination {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    height: 20px;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    list-style-type: none;
    padding: 0;
    margin: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.5);
    & li {
        width: 10px;
        height: 10px;
        background-color: $color-grey;
        border-radius: 100%;
    }
    & .wrong {
        background-color: $color-wrong-answer;
    }

    & .right {
        background-color: $color-right-answer;
    }
}

.pictures-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 20px;
    height: 372px;
}

.answer {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 10px;
}

#variant1 {
    background-image: url('https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg');
}

#variant2 {
    background-image: url('https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg');
}

#variant3 {
    background-image: url('https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg');
}

#variant4 {
    background-image: url('https://raw.githubusercontent.com/OlgaTolpykina/image-data/master/img/0.jpg');
}

@media (max-width: 580px) {
    .artists-answers {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(4, 50px);
    }
}

@media (max-width: 460px) {
    .timer-input {
        width: 190px;
    }

    .artists-picture {
        width: 280px;
        height: 280px;
    }
}