html {
  font-size: 10px;
}

body {
  margin: 0;
  background-color: $color-background;
  color: $color-text-on-dark;
  font-size: 2rem;
}

button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  font-size: 24px;
}

.hidden {
  opacity: 0;
}

.application {
  // height: calc(100vh - 20px);
  width: calc(100vw - 35px);
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 20px;
}

.container {
  position: relative;
  left: 0;
  transition: left 2s ease;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
  margin-bottom: 10px;
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-height: 550px;
  margin-bottom: 10px;
}

.content div {
  width: 100%;
  &.category-image {
    width: 195px;
  }
  &.artists-picture {
    width: 480px;
  }
  &.artists-answers {
    width: 80%;
  }
  &.pictures-wrapper {
    width: 480px;
  }
}

.hide {
  left: -2000px;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // height: 10%;
  // position: absolute;
  // bottom: 0;
  width: calc(100vw - 50px);
  max-width: 1440px;
}

.footer-logo {
  width: 85px;
  height: 31px;
  background-image: url('../img/svg/rs_school.svg');
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 170px;
  & a {
    text-decoration: none;
    color: $color-text-on-dark;
  }
}
